.header {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 90vh;
}
.header__ct {
  display: flex;
}
.header__text {
  display: flex;
  align-items: center;
  width: 50%;
  height: 100%;
}

.header__image {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 50%;
  height: 100%;
}

.header__title {
  font-family: Plus Jakarta Sans;
  font-size: 56px;
  font-weight: 700;
  line-height: 78px;
  width: 100%;
  max-width: 550px;
  text-align: left;
}

.header__pretitle {
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0px;
  width: 100%;
  max-width: 537px;
  text-align: left;
  padding-top: 30px;
  padding-bottom: 15px;
}
.header__btn {
  font-family: Plus Jakarta Sans;
  padding: 20px 40px 20px 40px;
  border-radius: 6px;
  gap: 10px;
  border: none;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  text-align: center;
  background: #1aab7b;
  color: white;
}
.header__btn:hover {
  border-radius: 6px;
  border: 1.5px solid #1aab7b;
  background: #fff;
  color: #1aab7b;
}
.header__btn:active{
  background: #1aab7b;
  color: #fff;
  opacity: 0.699999988079071;
}