.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10vh;
  background-color: #eeeef4;
  border-bottom: 0.2px solid;
}
.logo:hover {
  opacity: 0.800000011920929;
}
.logo:active {
  opacity: 0.6000000238418579;
}
.nav__ct {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__listsss {
  display: flex;
  gap: 20px;
}

.nav__anker {
  font-size: 16px;
  font-weight: 500;
  line-height: 26px;
  font-family: "Plus Jakarta Sans", sans-serif;
  color: #000;
}

.nav__num {
  width: 156px;
  height: 41px;
  border-radius: 6px;
  padding: 12px, 24px, 12px, 24px;
  gap: 10px;
  font-size: 14px;
  color: white;
  background-color: #000;
}
.nav__num:hover {
  border: 1px solid #1aab7b;
  background: #fff;
  color: #1aab7b;
}
.nav__num:active {
  color: white;
  background-color: #000;
  opacity: 0.6000000238418579;
}
.nav__anker:hover {
  color: #1aab7b;
  font-size: 16px;
  font-family: Plus Jakarta Sans;
  font-weight: 500;
  line-height: 150%;
}
.nav__item:active {
  color: #1aab7b;
  font-size: 16px;
  font-family: Plus Jakarta Sans;
  font-weight: 500;
  line-height: 150%;
  opacity: 0.6000000238418579;
}
