.main {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 1600px;
}
.main__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 15%;
}
.main__title {
  font-family: Plus Jakarta Sans;
  font-size: 36px;
  font-weight: 600;
  line-height: 50px;
  letter-spacing: 0px;
  text-align: center;
}
.main__pretitle {
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
  width: 650px;
  padding-top: 20px;
}

.special__card--title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
}
.card__with--text {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main__card--title {
  font-family: Plus Jakarta Sans;
  font-size: 40px;
  font-weight: 700;
  line-height: 56px;
  letter-spacing: 0px;
  text-align: left;
  max-width: 488px;
  width: 100%;
}
.main__pretitle {
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0px;
  text-align: left;
  max-width: 592px;
  width: 100%;
}

.text{
  width: 60%;
}
.main__text--ct {
  width: 100%;
}
