.footer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 400px;
  background-color: #101010;
}
.footer__text1 {
  display: flex;
  align-items: center;
  width: 50%;
}
.upside {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 80%;
  border-bottom: 1px solid #3b4155;
}
.footer__flex {
  display: flex;
}
.down {
  display: flex;
  justify-content: center;
  height: 20%;
}
.footer__contact {
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;
  width: 50%;
}

.logos {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 170px;
  width: 100%;
  padding-top: 20px;
}
.footer__contact--list {
  padding: 0;
  margin: 0;
}

.footer__contact--item {
  color: #a1a8b7;
  font-size: 18px;
  font-family: Plus Jakarta Sans;
  font-weight: 500;
  line-height: 150%;
  padding: 3px;
}
.footer__ct {
  display: flex;
  flex-direction: column;
  width: 40%;
}
.white {
  color: white;
  font-size: 20px;
}
.service__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40%;
  height: 70%;
  padding: 0;
  margin: 0;
}
.resourses__list {
  height: 70%;
  width: 60%;
}
.service__item {
  color: #a1a8b7;
  font-size: 18px;
  font-family: Plus Jakarta Sans;
  font-weight: 500;
  line-height: 150%;
  width: 100%;
  padding-top: 3px;
  padding-top: 10px;
}
.service__item:hover,
.resourses__item:hover {
  color: #1aab7b;
}
.service__item:active,
.resourses__item:active {
  opacity: 0.800000011920929;
}
.resourses__item {
  color: #a1a8b7;
  font-size: 18px;
  font-family: Plus Jakarta Sans;
  font-weight: 500;
  line-height: 150%;
  padding-top: 3px;
  padding-top: 10px;
}
.footer__recourses {
  width: 50%;
}
.cp-right {
  font-size: 18px;
  font-family: Plus Jakarta Sans;
  font-weight: 500;
  line-height: 150%;
  color: #fff;
}

.center {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
svg:hover path{
  fill: #1aab7b;
}