.contact {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 900px;
}
.container-sm {
  max-width: 1000px;
  width: 100%;
}
.form {
  display: flex;
  flex-direction: column;
}
.contact__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 30%;
  padding-bottom: 30px;
}
.contact__text--title {
  text-align: center;
  font-family: Plus Jakarta Sans;
  font-size: 36px;
  font-weight: 700;
  line-height: 50px;
  letter-spacing: 0px;
  text-align: center;
  max-width: 411px;
  width: 100%;
}

.contact__text--pretitle {
  text-align: center;
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: center;
  max-width: 650px;
  width: 100%;
  padding-top: 10px;
}
/*  */
/*  */
.contact__form {
  width: 100%;
  height: 60%;
  background: #f2f4f6;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80%;
}
.contact__name {
  width: 384px;
  height: 46px;
  border: none;
  border-bottom: 1px solid #636f87;
  background: transparent;
}
.contact__name::placeholder {
  font-family: Plus Jakarta Sans;
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0em;
  text-align: left;
  color: black;
}
.contact__number {
  width: 384px;
  height: 46px;
  border: none;
  border-bottom: 1px solid #636f87;
  background: transparent;
}
.contact__number::placeholder {
  font-family: Plus Jakarta Sans;
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0em;
  text-align: left;
  color: black;
}
.contact__description {
  width: 384px;
  height: 46px;
  border: none;
  border-bottom: 1px solid #636f87;
  /*  */
  font-family: Plus Jakarta Sans;
  font-size: 20px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0em;
  text-align: left;
  color: black;
  background: transparent;
}
option {
  font-family: Plus Jakarta Sans;
  font-size: 17px;
  font-weight: 700;
  line-height: 25px;
  letter-spacing: 0em;
  text-align: left;
  color: black;
}
.contact__submit {
  width: 180px;
  height: 59px;
  border-radius: 6px;
  gap: 10px;
  padding: 20px 40px 20px 40px;
  border: none;
  background: #1aab7b;
  /*  */
  /*  */
  font-family: Plus Jakarta Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
  letter-spacing: 0px;
  text-align: center;
  color: #fff;
}
.alert__text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 20%;
}
.form__text {
  font-family: Plus Jakarta Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  text-align: center;
  color: #858ea1;
}
