.section {
  display: flex;
  justify-content: center;
  background-color: #f2f4f6;
  width: 100%;
  height: 937px;
}
.section__cards {
  display: grid;
  grid-template-columns: auto auto auto;
  row-gap: 40px;
}
.section__title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 10%;
}
.sect__title {
  font-family: Plus Jakarta Sans;
  font-size: 36px;
  font-weight: 600;
  line-height: 50px;
  letter-spacing: 0px;
  text-align: center;
}

.section__view--anker {
  font-family: Plus Jakarta Sans;
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
  text-decoration: underline;
}
.section__card--title {
  font-family: Plus Jakarta Sans;
  font-size: 32px;
  font-weight: 600;
  line-height: 45px;
  letter-spacing: 0px;
  text-align: left;
  color: #fff;
}
.section__pretitle {
  width: 100%;
  max-width: 324px;
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
  padding-top: 10px;
  padding-bottom: 20px;
}
.section__view--anker {
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #fff;
}

.section__webDizayn {
  display: flex;
  justify-content: center;
  width: 384px;
  height: 358px;
  border-radius: 6px;
  background: #1aab7b;
}
.title-logo {
  display: flex;
  align-items: end;
  justify-content: space-between;
  width: 100%;
  height: 25%;
}
.cards__ct {
  width: 100%;
  max-width: 310px;
}

.section__CARD {
  display: flex;
  justify-content: center;
  width: 384px;
  height: 358px;
  border-radius: 6px;
  background-color: #fff;
  box-shadow: 0px 0px 10px gray;
}

.section__CARD--title {
  font-family: Plus Jakarta Sans;
  font-size: 32px;
  font-weight: 600;
  line-height: 45px;
  letter-spacing: 0px;
  text-align: left;
}

.section__CARD--pretitle {
  font-family: Plus Jakarta Sans;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  padding-top: 10px;
  padding-bottom: 20px;
}
.section__CARD--anker {
  font-family: Plus Jakarta Sans;
  font-size: 20px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: 0em;
  text-align: left;
  color: black;
}
